@import '/src/shared/styles';

.schemaPage {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: calc(#{$nav-bar-height-mobile}) 0px 105px;
  box-sizing: border-box;
  background: $lightGray;

  .addBtn {
    bottom: 20px;
    left: 20px;
    position: absolute;
    z-index: 1;
    transition: 2s $ease;
  }

  li {
    transition: opacity tranform 1s $ease;
  }

  &.sorting {
    li {
      pointer-events: none;
      opacity: 0.5;
    }
  }
}

.itemWrapper {
  height: auto;
  text-align: left;
  border-radius: 0;
  padding: 24px 33px;
  background: $white;
  margin-bottom: 7px;
  border: none;
}

.label {
  padding-bottom: 0.5em;
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.4em;

  & > * {
    text-decoration: underline;
  }
}

.dragging {
  @extend %dragging-helper;
}

:global {
  .schema-item-group {
    font-weight: 700;
  }
}
